@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Inconsolata:wght@400;500&family=Noto+Serif&family=Rubik&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family:  'Noto Serif', serif;
  font-weight: 500;
  background-color: rgb(34, 34, 34);  
}

::-webkit-scrollbar {
	width:10px;
	height: 10px;
 }

 ::-webkit-scrollbar-track {
	background:#333;
}

::-webkit-scrollbar-thumb {
	background: #2e9dd8;
}


:root {
    --clr-neutral-900: hsl(207, 19%, 9%);
    --clr-neutral-100: hsl(0, 0%, 100%);
    --clr-neutral-400: hsl(177, 90%, 61%);
    --grad-color: linear-gradient(90deg, rgba(187, 167, 187, 1) 0%, rgba(140, 124, 132, 0.93) 43%, rgba(140, 34, 34, 1) 100%);
  
    --padding: 1.5rem;
}

h1 {
  margin-top: 30px;
  margin-bottom: 30px;
  background-image: var(--grad-color);
  color: black;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;
  /* align-items: center; */
}